import config from '../config.js'; // SENÄ°N CONFIG DOSYAN

const { TMDB_API_KEY, TMDB_BASE_URL } = config;

async function setLibraryHeroBackground() {
  const heroElement = document.querySelector('.library-hero-bg');
  if (!heroElement) {
    console.log('library-hero-bg bulunamadÄ±');
    return;
  }

  try {
    // 1. KÃ¼tÃ¼phaneyi al
    const raw = localStorage.getItem('myMovieLibrary');
    if (!raw) {
      console.log('KÃ¼tÃ¼phane yok â€“ fallback kullanÄ±lÄ±yor');
      return;
    }

    const library = JSON.parse(raw);
    if (!Array.isArray(library) || library.length === 0) {
      console.log('KÃ¼tÃ¼phane boÅŸ â€“ fallback kullanÄ±lÄ±yor');
      return;
    }

    // 2. Rastgele film seÃ§ (sadece ID olabilir)
    const randomItem = library[Math.floor(Math.random() * library.length)];
    const movieId = typeof randomItem === 'number' ? randomItem : randomItem.id;

    if (!movieId) {
      console.log('GeÃ§erli ID yok');
      return;
    }

    console.log('KÃ¼tÃ¼phaneden seÃ§ilen film ID:', movieId);

    // 3. SENÄ°N CONFIGâ€™Ä°NLE APIâ€™DEN TAM FÄ°LM BÄ°LGÄ°SÄ°NÄ° Ã‡EK
    const res = await fetch(
      `${TMDB_BASE_URL}/movie/${movieId}?api_key=${TMDB_API_KEY}`
    );

    if (!res.ok) {
      console.log('API hatasÄ±:', res.status);
      return;
    }

    const movie = await res.json();

    // 4. Backdrop varsa â†’ arka plan yap (senin hero.js ile aynÄ± yapÄ±)
    const backdropPath = movie?.backdrop_path;
    if (backdropPath) {
      const imageUrl = `https://image.tmdb.org/t/p/w1920${backdropPath}`;
      heroElement.style.backgroundImage = `url('${imageUrl}')`;
      heroElement.style.backgroundSize = 'cover';
      heroElement.style.backgroundPosition = 'center';
      heroElement.style.backgroundRepeat = 'no-repeat';
      console.log('Library hero yÃ¼klendi:', movie.title);
    } else {
      console.log('Backdrop yok:', movie.title);
    }
  } catch (error) {
    console.error('Library hero gÃ¶rseli yÃ¼klenemedi:', error);
  }
}

// HEMEN Ã‡ALIÅžTIR (partial olduÄŸu iÃ§in DOMContentLoaded beklemeye gerek yok)
setLibraryHeroBackground();
